<template>

	<view class="content">
		
		<view class="pl30 pt35 pr30 mt12">
			<view class="flex b-b-1 pb5">
				<text class="icon-shouji iconfont fs20 c2"></text>
				<!-- <input  class="pupu_shouj" type="text" placeholder="用户名/手机号" placeholder-class="c2 pl10 fs16"/> -->
				<input placeholder-class="c2 fs16 pupu_shouj" type="text" placeholder="请输入手机号"
					class="pupu_shouj c2 pl10 fs16" />
			</view>
			<view class="flex b-b-1 pt30">
				<text class="icon-mima1 iconfont c2 "></text>
				<input placeholder-class="c2  fs16 " type="text" class="pupu_shoujs c2 pl10 fs16" placeholder="验证码" />
				<button @click='codeClick' v-if='changess' class="fs12 bgcf" style="color: #FF1919;"> 获取验证码</button>
				<button @click='codeClick' :disabled='disabled' v-else class="fs12 "
					style="background-color: #ffffff;">{{second}}后重试</button>
			</view>
			
		</view>
		<view class="funs fs17 flex aic ju_c cf "@click="denglu">
			登录
		</view>
		
	</view>
</template>

<script>
	import api from '../../components/service.js';
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		name: "login",
		data() {
			return {
				showText: true,
				second: 59,
				disabled: false,
				changess: true,
				usr_pwd: "",
				seen: false,
				type_text: "text",
				type_password: "password",
			};
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			fanhui() {
				wx.navigateBack({
					delta: 1
				})
			},
			codeClick() {
			
				that.changess = false;
				that.disabled = true
				// console.log("1")
				that.showText = false
				var interval = setInterval(() => {
					--that.second
				}, 1000)
			
				setTimeout(() => {
					clearInterval(interval)
					that.showText = true
					that.second = 60
					that.disabled = false
					that.changess = true;
				}, 60000)
			
			
			},
			changeSeen() {
				this.seen = !this.seen;
			},
			denglu() {
				uni.navigateTo({
					url: '../loge/loge',
				});
			},
			// 获取验证码
			codeClick() {

				that.changess = false;
				that.disabled = true
				// console.log("1")
				that.showText = false
				var interval = setInterval(() => {
					--that.second
				}, 1000)

				setTimeout(() => {
					clearInterval(interval)
					that.showText = true
					that.second = 60
					that.disabled = false
					that.changess = true;
				}, 60000)


			},
		}
	}
</script>

<style>
	.c2 {
		color: #c2c2c2;
	}

	page {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.content {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.funs {
		width: 600rpx;
		height: 90rpx;
		background: #FF1919;
		border-radius: 45rpx;
		margin-left: 70rpx;
		margin-top: 80rpx;
	}
	.weix {
		width: 88rpx;
		height: 88rpx;
		background: #71CB44;
		border-radius: 50%;
	}
</style>
